$colors: (
    "light-gray": #eee,
    "gray": #888,
    "dark-gray": #393939,
    "blue": #009fff,
    "db": #004183,
    "lb": #6EC5E9,
    "nb": #091F92,
    "green": #00af33,
    "teal": #05e9ff,
    "purple": #9f79ee,
    "lg": #88c538,
    "olive": #73b1b7,
    "red": #b22222,
    "orange": #fe7621,
    "gold": #FFD800,
    "pink": #FFBAD2,
);

// "db": #3081b1
// "lb": #5882aa
// "gold": #a9884f
// "pink": #945257
$dark-colors: (
    "light-gray": #bbb,
    "gray": #5F5F5F,
    "dark-gray": #090909,
    "blue": #0E75b3,
    "db": #00274E,
    "lb": #5DA7C6,
    "nb": #06176D,
    "green": #10792e,
    "teal": #0faebe,
    "purple": #7f65b5,
    "lg": #6b9533,
    "olive": #557A7e,
    "red": #882323,
    "orange": #c76722,
    "gold": #D9B700,
    "pink": #CC94A8,
);

$include-colors: (
  'blue', 'purple'
);

$white: map-get($colors, "light-gray");
$white-dark: map-get($dark-colors, "light-gray");
$gray: map-get($colors, "gray");
$gray-dark: map-get($dark-colors, "gray");
$dark-gray: map-get($colors, "dark-gray");
$dark-gray-dark: map-get($dark-colors, "dark-gray");
