@import "jass/misc/helpers/mixins";

/*

b = "bottom"
c = "center"
d = "display"
  b = "block"
  ib = "inline-block"
  t = "table"
  tc = "table-cell"
h = "height"

l = "left"
r = "right"
t = "top"
ta = "text-align"

w = "width"
z = "z-index"
*/

@import "micro/background";

// Coordinates
.top-0 {
  top: 0;
}
.bottom-0 {
  bottom: 0;
}
.bottom-100 {
  bottom: 100%;
}
.left-0 {
  left: 0;
}
.right-0 {
  right: 0;
}

// Display
.d-b {
  display: block;
}
.d-ib {
  display: inline-block;
}
.d-none {
  display: none;
}

// Height
@each $key, $value in ("100": 100%) {
  .h-#{$key} {
    height: $value;
  }
}

// Width
@each $key, $value in ("100": 100%) {
  .w-#{$key} {
    width: $value;
  }
}

// Float
.float-l {
  float: left;
}

.float-r {
  float: right;
}

.clear:after {
  content: " ";
  display: block;
  clear: both;
}

// Min-Height
.min-h-100 {
  min-height: 100%;
}

// List-Style
.ls-none {
  list-style: none;
}

// Opacity
// @each $key, $value in ("0": 0, "40": 0.4) {
//  .o-#{$key} {
//    opacity: $value;
//  }
// }

// Margin
.ma-0 {
  margin: 0;
}

.mt-0 {
  margin-top: 0;
}

.mb-0 {
  margin-bottom: 0;
}

.ml-0 {
  margin-left: 0;
}

.mb-1 {
  margin-bottom: 0.25em;
}

.ml-40 {
  margin-left: 40px;
}

// Padding
// 40, 45, 80)
@each $value in (16) {
  .pad-#{$value} {
    padding: #{$value}px;
  }

  .pad-t-#{$value} {
    padding-top: #{$value}px;
  }
  
  .pad-b-#{$value} {
    padding-bottom: #{$value}px;
  }

  .pad-l-#{$value} {
    padding-left: #{$value}px;
  }

  .pad-r-#{$value} {
    padding-right: #{$value}px;
  }
}

// Position
.pos-abs {
  position: absolute;
}
.pos-rel {
  position: relative;
}

// Text Align
.ta-c {
  text-align: center;
}

// Font Weight
.fw-bold {
  font-weight: bold;
}

// Transition
.trans-o-03 {
  @include transition(opacity .3s);
}
.trans-o-04 {
  @include transition(opacity .4s);
}
.trans-o-v-04 {
  @include transition(visibility 0s, opacity .4s);
}

// Vertical Align
.va-m {
  vertical-align: middle;
}

// Z-Index
.z-1 {
  z-index: 1;
}
.z--1 {
  z-index: -1;
}
