@import "jass/misc/colors/colors";
@import "jass/misc/helpers/mixins";
@import "plugins/nprogress";

@import "micro";

@each $name in ('purple', 'teal', 'blue', 'lg', 'red', 'orange', 'gold') {
  .#{$name} {
    color: map-get($dark-colors, $name) !important;
  }
  .#{$name}:hover {
    color: map-get($colors, $name) !important;
  }
}

.jp {
  font-family: 'ヒラギノ角ゴ Pro W3', Osaka, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  font-weight: normal;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

a {
  background: transparent;
  color: map-get($dark-colors, 'light-gray');
  text-decoration: none;
  
  @include transition(color .3s);
  
  &:active, &:hover {
    color: map-get($colors, 'light-gray');
    outline: 0;
  }
}

img {
  border: 0;
}

b,
strong {
  font-weight: bold;
}

h1 {
  font-size: 3em;
  font-weight: 300;
  line-height: 1;
  letter-spacing: 3px;
  margin: 0 0 16px;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.7);
  text-transform: uppercase;
}

p {
  line-height: 1.35;
  margin: 10px 0;
}

body {
  background-color: #101010;
  color: $white;
  font-family: "Lato", "Helvetica Neue", Helvetica, sans-serif;
  font-size: 22px;
  margin: 0;
  
  @media (max-device-width: 480px) {
    font-size: 18px;
  }

  @include antialiased;
}

#page-header {
  background: rgba(0, 0, 0, 0.5);
  font-size: 18px;
  line-height: 24px;  
  @include linear-gradient(rgba(0, 0, 0, 0.6), transparent);
}

.home {
  color: #fff;
  font-size: 16px;

  &:hover {
    opacity: 0.8;
  }
}

#nav-wrap {
  a {
    color: #aaa;

    &:hover {
      color: #fff;
    }
  }
}

// #main-wrap
#main-wrap {
  // get rid of weird chrome flicker
  @include backface-visibility-hidden;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background: rgba(0, 0, 0, 0.4);
  min-height: 560px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
}

.main-content {
  // get rid of weird chrome flicker
  @include backface-visibility-hidden;

  margin: 0 auto;
  max-width: 720px;
  padding-bottom: 50px;
}

.social {
  font-size: 0px;
  margin: 24px 0 16px;

  a {
    color: $white !important;
    display: inline-block;
    margin: 0 16px;
    // @include filter( drop-shadow(1px 1px 3px rgba(0, 0, 0, 0.7)) );

    &:hover {
      color: $white-dark !important;
    }
  }
  
  .icon {
    fill: currentColor;
    height: 28px;
    width: 28px;
  }
}

.footer {
  font-size: 18px;
  line-height: 1.6;
}

#background {
  @include transition(filter .4s);
  &.grayscale {
    @include filter(grayscale(100%));
  }
}

#hex {
  background: url(/assets/images/hex.svg) center;
}

#credit {
  @include border-box;
  @include translate(0, -100%);

  font-size: 18px;
  line-height: 1;
  position: absolute;
  right: 0;
  text-align: right;
  text-shadow: 1px 1px 22px rgba(0, 0, 0, 0.7), 0 1px 3px rgba(0, 0, 0, .7);
  
  @media (max-width: 768px) {
    text-align: center;

    &.credit-long {
      .by {
        font-size: 0;
        display: block;
      }
    }
  }
}

.credit-social {
  @include transition(opacity .2s);
  
  font-size: 20px;
  margin-bottom: 8px;
  
  a {
    // @include filter( drop-shadow( 1px 1px 6px rgba(0, 0, 0, 0.7) ) );
    margin-left: 10px;
  }
  
  .icon {
    fill: currentColor;
    height: 20px;
    width: 20px;
  }
  
  .icon-facebook {
    margin-top: -2px !important;
  }
  
  &:after {
    content: "";
    display: block;
    height: 8px;
    position: absolute;
    bottom: -8px;
    left: 0;
    right: 0;
  }
  
  @media (max-width: 768px) {
    @include transition(opacity 0s);

    display: block !important;
    padding: 8px 0 0;
    opacity: 1 !important;
    position: static;
    
    a {
      float: none;
      margin: 0 5px;
    }

    .icon-facebook {
      margin-left: -2px;
    }
    
    &:after {
      display: none;
    }
  }
}

@media (max-width: 768px) {
  .credit-title, .credit-name {
    line-height: 1.4;
  }
}
